import React, { Component } from 'react';
import logo from '../assets/logo.png';
import { Link } from "react-router-dom";


class NavBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuActiveClass: ""
    }

    this.handleBurgerClick = this.handleBurgerClick.bind(this);
    this.menuItemClick = this.menuItemClick.bind(this);
  }

  handleBurgerClick(e) {
    this.setState({
      menuActiveClass : this.state.menuActiveClass === "" ? "is-active" : ""
    })
  }

  menuItemClick(e) {
    this.setState({
      menuActiveClass : ""
    })
  }

  render() {
    return (
      <nav className="navbar is-fixed-top is-transparent" role="navigation" aria-label="main navigation">
        <div className="navbar-brand">
          <Link className="navbar-item" exact="true" to="/">
            <img className="logo" src={logo} width="35" />
            <span className="navbar-item logo-text">FARMLAND</span>
          </Link>
          <a onClick = { this.handleBurgerClick }
            role="button"
            className={`navbar-burger burger ${this.state.menuActiveClass}`}
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar-menu-items">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div id="navbar-menu-items" className={`navbar-menu ${this.state.menuActiveClass}`} onClick = { this.menuItemClick }>
          <div className="navbar-start">
            <Link className="navbar-item" to="/farm" >Farm</Link>
            <Link className="navbar-item" to="/docs">Docs</Link>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="buttons is-centered">
                <a className="button is-primary">{this.props.contracts.landBal} LAND</a>
                <a className="button is-info">{this.props.contracts.ethBal} ETH</a>
                <a className="button is-dark">
                  {this.props.contracts.walAddress}&nbsp;<ion-icon name="link-outline"></ion-icon>
                </a>
              </div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default NavBar;
