import React, { Component } from 'react';
import { Decoration, Tree } from './vectors';

class Farm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      plantFieldAmount: 0,
      compostCornAmount: 0
    };

    this.handlePlantFieldAmount = this.handlePlantFieldAmount.bind(this);
    this.maxPlantFieldAmount = this.maxPlantFieldAmount.bind(this);
    this.allocateLand = this.allocateLand.bind(this);
    this.ploughField = this.ploughField.bind(this);
    this.harvestCorn = this.harvestCorn.bind(this);
    this.handleCompostCornAmount = this.handleCompostCornAmount.bind(this);
    this.maxCompostCornAmount = this.maxCompostCornAmount.bind(this);
    this.compostCorn = this.compostCorn.bind(this);
  }

  // Plant Field
  handlePlantFieldAmount(e) {
    this.setState({ plantFieldAmount: e.target.value });
  }

  maxPlantFieldAmount() {
    this.setState({ plantFieldAmount: this.props.contracts.landBal });
  }

  allocateLand() {
    this.props.ethereumservice.plantField(this.state.plantFieldAmount);
  }

  // Plough Field
  ploughField() {
    this.props.ethereumservice.ploughField();
  }

  // Harvest Corn
  harvestCorn() {
    this.props.ethereumservice.harvestCorn();
  }

  // Compost Corn
  handleCompostCornAmount(e) {
    this.setState({ compostCornAmount: e.target.value });
  }

  maxCompostCornAmount() {
    this.setState({ compostCornAmount: this.props.contracts.cornBalTrue });
  }

  compostCorn() {
    this.props.ethereumservice.compostCorn(this.state.compostCornAmount);
  }

  render() {
    return (
      <section id="farm" className="is-desktop is-widescreen is-vcentered hero is-fullheight">

        <div className="hero-body">
          <div className="columns is-vcentered">
            <div className="decoration column is-hidden-touch is-hidden-desktop-only is-hidden-widescreen-only">
              <Tree id="left-tree" width={200} height={300} {...this.props} />
            </div>

            {/* style={{ opacity: overlaid ? 1 : 0 }} */}
            <div className="main column is-half-fullhd is-full-widescreen">
              <div className="columns is-mobile is-multiline is-vcentered">
                <div className="decoration column is-full mb-6">
                  <Decoration width={300} height={150} />
                </div>

                {!this.props.contracts.isLoaded && (
                  <div className="column is-full">
                    <div className="balance-info columns is-mobile">
                      <div className="column-info column is-full">
                        <div className="columns is-multiline">
                          <span className="column info-value">
                            <h2 className="info">Connecting to wallet...</h2>
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                )}

                {this.props.contracts.isLoaded && (
                  <div className="column is-full">
                    {!this.props.contracts.isAuthorized && (
                      <div className="balance-info columns is-mobile pb-6">
                        <div className="column-info column action">
                          <button
                            className="button is-rounded is-primary"
                            onClick={() => this.props.ethereumservice.approveContract()}>
                            Authorize Land
                      </button>
                        </div>
                      </div>
                    )}

                    {this.props.contracts.isAuthorized && (
                      <div className="balance-info columns is-desktop">
                        <div className="column">
                          <div className="columns is-mobile is-multiline">
                            <div className="column-info column is-full">
                              <div className="columns is-multiline">
                                <span className="column info-value">
                                  <h2 className="info">Unallocated land :</h2>
                                  <h2 className="value">{this.props.contracts.landBal} LAND</h2>
                                </span>

                                <div className="column is-full-mobile field has-addons has-addons-right info-action">
                                  <div className="control">
                                    <button className="button is-rounded  is-small" onClick={this.maxPlantFieldAmount}>
                                      Max
                                </button>
                                  </div>

                                  <div className="control">
                                    <input
                                      className="input is-rounded is-primary is-small"
                                      type="text"
                                      placeholder="LAND Amount"
                                      value={this.state.plantFieldAmount}
                                      onChange={this.handlePlantFieldAmount}
                                    />
                                  </div>
                                  <div className="control">
                                    <button className="button is-rounded is-primary is-small" onClick={this.allocateLand}>
                                      Allocate Land
                                </button>
                                  </div>
                                </div>
                              </div>
                            </div>

                            <div className="column-info column is-full">
                              <div className="columns is-multiline">
                                <span className="column info-value">
                                  <h2 className="info">Farm land :</h2>
                                  <h2 className="value">{this.props.contracts.lField} LAND</h2>
                                </span>

                                <div className="column is-full-mobile info-action">
                                  <button className="button is-rounded is-primary is-small" onClick={this.ploughField}>
                                    Release Land
                              </button>
                                </div>
                              </div>
                            </div>

                            <div className="column-info column is-full">
                              <div className="columns is-multiline">
                                <span className="column info-value">
                                  <h2 className="info">Corn growing :</h2>
                                  <h2 className="value">{this.props.contracts.cHarvest} CORN</h2>
                                </span>

                                <div className="column is-full-mobile  info-action">
                                  <button className="button is-rounded is-primary is-small" onClick={this.harvestCorn}>
                                    Harvest Corn
                              </button>
                                </div>
                              </div>
                            </div>

                            <div className="column-info column is-full">
                              <div className="columns is-multiline">
                                <span className="column info-value">
                                  <h2 className="info">Available corn :</h2>
                                  <h2 className="value">{this.props.contracts.cornBal} CORN</h2>
                                </span>

                                <div className="column is-full-mobile field has-addons has-addons-right info-action">
                                  <div className="control">
                                    <button className="button is-rounded  is-small" onClick={this.maxCompostCornAmount}>
                                      Max
                                </button>
                                  </div>

                                  <div className="control">
                                    <input
                                      className="input is-rounded is-primary is-small"
                                      type="text"
                                      placeholder="CORN Amount"
                                      value={this.state.compostCornAmount}
                                      onChange={this.handleCompostCornAmount}
                                    />
                                  </div>
                                  <div className="control">
                                    <button className="button is-rounded is-primary is-small" onClick={this.compostCorn}>
                                      Compost Corn
                                </button>
                                  </div>
                                </div>
                              </div>
                            </div>

                            {/* <div className="column-info column is-full">
                          <h2 className="info">Base crop growth rate? : </h2>
                          <h2 className="value">0.0002</h2>
                        </div> */}

                            <div className="column-info column is-full">
                              <h2 className="info">Last harvest : </h2>
                              <h2 className="value">{this.props.contracts.blockNumber}</h2>
                            </div>

                            <div className="column-info column is-full">
                              <h2 className="info">Composting boost : </h2>
                              <h2 className="multiplier">x</h2>
                              <h2 className="value">{this.props.contracts.farmCompostBoost}</h2>
                            </div>

                            <div className="column-info column is-full">
                              <h2 className="info">Farm maturity boost : </h2>
                              <h2 className="multiplier">x</h2>
                              <h2 className="value">{this.props.contracts.farmMaturityBoost}</h2>
                            </div>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                )}

                <div className="decoration column is-full mt-6">
                  <Decoration width={300} height={150} rotate={180} />
                </div>
              </div>
            </div>

            <div className="decoration column is-hidden-touch is-hidden-desktop-only is-hidden-widescreen-only">
              <Tree id="right-tree" width={200} height={300} {...this.props} />
            </div>
          </div>
        </div>
      </section >
    );
  }
}

export default Farm;
