import { ethers } from 'ethers';
import { Subject } from 'rxjs';

import landToken from '../contracts/Land.json';
import cornToken from '../contracts/Corn.json';

const landContractAddress = '0x112b2060E01000194d13aF3B15874fF5599cD0f6';
const cornContractAddress = '0xF9359F511c8AdE308B977195b1Ba15b81c325254';

export class EthereumService {
  constructor() {
    this.contracts = {
      isAuthorized: false,
      isPending: false
    };

    this.hasError = false;
    this.errorMessage = '';
    this.events = new Subject();
    this.errors = new Subject();

    if (typeof window.ethereum !== 'undefined' || typeof window.web3 !== 'undefined') {
      try {
        window.ethereum.enable().then((this.provider = new ethers.providers.Web3Provider(window.ethereum)));
        this.signer = this.provider.getSigner();
        this.land = new ethers.Contract(landContractAddress, landToken.abi, this.signer);
        this.corn = new ethers.Contract(cornContractAddress, cornToken.abi, this.signer);

        this.setupEvents();
        this.events.next();
      } catch (e) {
        this.initError = true;
      }
    }
  }

  loadBalances() {
    var parseValue = (value, units, decimals) => {
      return value ? parseFloat(ethers.utils.formatUnits(value, units)).toFixed(decimals) : 0;
    };

    return this.signer.getAddress().then(address =>
      Promise.all([
        address,
        this.provider.getBalance(address),
        this.land.balanceOf(address),

        // getAddressDetails - [0]blockNumber, [1]cornBalance, [2]mintAmount, [3]addressTimeMultiplier, [4]addressBurnMultiplier, [5]globalLockedAmount, [6]globalBurnedAmount
        this.corn.getAddressDetails(address),

        // struct addressLocks {uint256 amount, uint256 burnedAmount; uint256 blockNumber; uint256 lastMintBlockNumber; address minterAddress;}
        this.corn.addressLocks(address),
        this.land.isOperatorFor(cornContractAddress, address)
      ]).then(
        response => {
          this.contracts = {
            isLoaded: true,
            walAddress: response[0],
            ethBal: parseValue(response[1], 18, 2),
            landBal: parseValue(response[2], 18, 2),
            blockNumber: parseValue(response[3] ? response[3][0] : null, 0, 0),
            cornBal: parseValue(response[3] ? response[3][1] : null, 18, 2),
            cornBalTrue: parseValue(response[3] ? response[3][1] : null, 18, 18),
            cHarvest: parseValue(response[3] ? response[3][2] : null, 18, 4),
            farmMaturityBoost: parseValue(response[3] ? response[3][3] : null, 4, 2),
            farmCompostBoost: parseValue(response[3] ? response[3][4] : null, 4, 2),
            lField: parseValue(response[4] ? response[4][0] : null, 18, 2),
            isAuthorized: response[5]
          };
        },
        error => this.errors.next({ message: 'Error connecting to wallet', error: error })
      )
    );
  }

  setupEvents() {
    this.provider.on('block', currentBlock => {
      this.loadBalances().then(() => {
        this.events.next();
      });
    });

    var update = message =>
      this.loadBalances().then(() => {
        this.events.next({ message: message });
      });

    this.corn.on('AuthorizedOperator', (cornContractAddress, walAddress) => {
      var message = 'Contract authorized';
      update(message);
    });

    this.corn.on('Planted', (cornContractAddress, currentBlock, walAddress, plantedAmount, burnedAmountIncrease) => {
      var message = 'LAND allocated to farm';
      update(message);
    });

    this.corn.on('Ploughed', (cornContractAddress, lField, burnedAmountDecrease) => {
      var message = 'LAND released from farm';
      update(message);
    });

    this.corn.on('Minted', (cornContractAddress, blockNumber, walAddress, preBlockNumber, cHarvest) => {
      var message = 'CORN harvested';
      update(message);
    });

    this.corn.on('Fertilized', (cornContractAddress, walAddress, fertilizedAmount) => {
      var message = 'CORN composted & farm land fertilized';
      update(message);
    });
  }

  approveContract() {
    this.loadBalances().then(() => {
      this.land.authorizeOperator(cornContractAddress, { from: this.contracts.walAddress }).then(
        () => this.events.next({ message: 'Authorization is pending', pending: true }),
        error => this.errors.next({ message: 'Error authorizing operator', error: error })
      );
    });
  }

  plantField(amount) {
    this.loadBalances().then(() => {
      var parsedAmount = ethers.utils.parseEther(amount.toString());
      this.corn.plant(this.contracts.walAddress, parsedAmount).then(
        () => this.events.next({ message: 'LAND allocation is pending', pending: true }),
        error =>
          this.errors.next({
            message: 'Error allocating LAND - To make a bigger farm please Release LAND first',
            error: error
          })
      );
    });
  }

  ploughField() {
    this.loadBalances().then(() => {
      this.corn.plough().then(
        () => this.events.next({ message: 'LAND release is pending', pending: true }),
        error => this.errors.next({ message: 'Error releasing LAND', error: error })
      );
    });
  }

  harvestCorn() {
    this.loadBalances().then(() => {
      this.provider.getBlockNumber().then(blockNumber => {
        this.corn.harvest(this.contracts.walAddress, this.contracts.walAddress, blockNumber).then(
          () => this.events.next({ message: 'CORN harvest is pending', pending: true }),
          error => this.errors.next({ message: 'Error harvesting CORN', error: error })
        );
      });
    });
  }

  compostCorn(amount) {
    this.loadBalances().then(() => {
      var parsedAmount = ethers.utils.parseEther(amount.toString());
      this.corn.fertilize(this.contracts.walAddress, parsedAmount).then(
        () => this.events.next({ message: 'CORN composting is pending', pending: true }),
        error => this.errors.next({ message: 'Error composting CORN', error: error })
      );
    });
  }
}
